jQuery(function () {

	/**
	 * Copy debug info to clipboard when the button is clicked.
	 */
	jQuery("#debug-info-button").on("click", () => {
		// Select text in textarea
		const textarea = jQuery("#debug-info-textarea")[0];
		textarea.select();
		textarea.setSelectionRange(0, 99999); // For mobile devices

		// Copy selected text to clipboard
		wpm.copyTextToClipboard(jQuery("#debug-info-textarea").val());

		// Show success message and hide after 3 seconds
		const successElement = jQuery("#debug-info-success");
		successElement.fadeIn(200);
		setTimeout(() => successElement.fadeOut(200), 3000);
	});

	jQuery("#pmw-pro-version-demo").on("click", function () {
		jQuery("#submit").trigger("click");
	});

	if (document.getElementById("json-settings-file-input")) {
		document.getElementById("json-settings-file-input")
			.addEventListener("change", wpm.readSettingsFile, false);
	}

	// if element ga4-data-api-credentials-upload-button exists, then we are on the GA4 settings page
	if (document.getElementById("ga4-data-api-credentials-upload-button")) {

		document.getElementById("ga4-data-api-credentials-upload-button")
			.addEventListener("change", wpm.readGa4DataApiCredentials, false);
	}

	// if element ga4-data-api-credentials-upload-button exists, then we are on the GA4 settings page
	if (document.getElementById("ga4-data-api-credentials-delete-button")) {

		document.getElementById("ga4-data-api-credentials-delete-button")
			.addEventListener("click", () => {
				wpm.saveGa4DataApiCredentialsToDb({});
			}, false);
	}

	/**
	 * Handle log files download via REST API
	 */
	jQuery("#wgact_download_logs_zip").on("click", function(e) {
		e.preventDefault();
		
		if (jQuery(this).prop("disabled")) {
			return;
		}
		
		const source = jQuery(this).data("source");
		const button = jQuery(this);
		const originalText = button.text();
		
		// Disable button and change text
		button.prop("disabled", true).text("Downloading...");
		
		// Use fetch with proper authentication
		fetch(pmwAdminApi.root + "pmw/v1/logs/download", {
			method: "POST",
			headers: {
				"Content-Type": "application/x-www-form-urlencoded",
				"X-WP-Nonce": pmwAdminApi.nonce
			},
			body: new URLSearchParams({
				source: source
			}),
			credentials: 'same-origin'
		})
		.then(response => {
			if (!response.ok) {
				throw new Error(`HTTP error! status: ${response.status}`);
			}
			return response.blob();
		})
		.then(blob => {
			// Create download link
			const url = window.URL.createObjectURL(blob);
			const a = document.createElement("a");
			a.href = url;
			a.download = "pmw-logs-" + new Date().toISOString().slice(0,19).replace(/:/g, "-") + ".zip";
			document.body.appendChild(a);
			a.click();
			window.URL.revokeObjectURL(url);
			document.body.removeChild(a);
			
			// Re-enable the button
			button.prop("disabled", false).text(originalText);
		})
		.catch(error => {
			console.error("Download error:", error);
			button.prop("disabled", false).text(originalText);
			alert("Error downloading log files. Please try again.");
		});
	});
});

